    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La animación turística no debe entenderse como simple entretenimiento, sino como una estrategia de educación vivencial orientada al aprendizaje significativo. Según Jensen (2015), la emoción es el motor del aprendizaje; por tanto, las experiencias deben despertar curiosidad, asombro y sentido de pertenencia. El guía debe mantener una actitud empática, entusiasta y flexible, ajustando su comunicación a las necesidades, ritmo y energía del grupo.
      .col-md-11
        h3 Lineamientos éticos y culturales en la animación turística
        .p La animación turística, especialmente en contextos étnicos y comunitarios, debe regirse por principios éticos y de respeto intercultural, que garanticen la dignidad de las comunidades anfitrionas y la coherencia con los valores del turismo sostenible. Los siguientes lineamientos orientan su práctica responsable:
    
    br
    .row.justify-content-center(data-aos="fade-right")
      .col-md-9.mb-5.mb-md-0
        LineaTiempoD
          .row(numero="1" titulo="Respeto a la identidad cultural:")
            .col-md-8.mb-4.mb-md-0              
              br
              .color-adicional-4.lh-1 Toda actividad debe reconocer y valorar las expresiones culturales locales sin convertirlas en espectáculos folclorizados. Las tradiciones y rituales deben presentarse con el consentimiento de los portadores culturales.
            .col-md-1
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/tema4/tema24-rec27-img1.svg', alt='Texto que describa la imagen')

