              .color-adicional-4.lh-1 Las comunidades deben intervenir activamente en la planificación, ejecución y evaluación de las actividades, con autorización expresa de sus representantes.
            .col-md-1
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/tema4/tema24-rec27-img2.svg', alt='Texto que describa la imagen')

